@font-face {
  font-family: "snicon-icon";
  src: url("/fonts/snicon.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: Oswald;
  src: url("/fonts/oswald-regular");
  src: url("/fonts/oswald-regular.eot") format("embedded-opentype"),
    url("/fonts/oswald-regular.woff") format("woff"),
    url("/fonts/oswald-regular.woff2") format("woff"),
    url("/fonts/oswald-regular.ttf") format("truetype"),
    url("/fonts/oswald-regular.svg") format("svg");
  font-weight: 400;
  font-style: 400;
}

@font-face {
  font-family: Oswald;
  src: url("/fonts/oswald-light");
  src: url("/fonts/oswald-light.eot") format("embedded-opentype"),
    url("/fonts/oswald-light.woff") format("woff"),
    url("/fonts/oswald-light.woff2") format("woff"),
    url("/fonts/oswald-light.ttf") format("truetype"),
    url("/fonts/oswald-light.svg") format("svg");
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Oswald;
  src: url("/fonts/oswald-bold");
  src: url("/fonts/oswald-bold.eot") format("embedded-opentype"),
    url("/fonts/oswald-bold.woff") format("woff"),
    url("/fonts/oswald-bold.woff2") format("woff"),
    url("/fonts/oswald-bold.ttf") format("truetype"),
    url("/fonts/oswald-bold.svg") format("svg");
  font-style: normal;
  font-weight: 700;
}

@font-face {
  font-family: "Roboto Condensed";
  src: url("/fonts/roboto-condensed-regular");
  src: url("/fonts/roboto-condensed-regular.eot") format("embedded-opentype"),
    url("/fonts/roboto-condensed-regular.woff") format("woff"),
    url("/fonts/roboto-condensed-regular.woff2") format("woff"),
    url("/fonts/roboto-condensed-regular.ttf") format("truetype"),
    url("/fonts/roboto-condensed-regular.svg") format("svg");
  font-weight: 400;
  font-style: normal;
}

.Toastify__toast {
  padding: 0 !important;

  &--default {
    background-color: transparent !important;
  }

  &-body {
    margin: 0 !important;
    border-radius: 5px;
  }
}

.Toastify__toast-container {
  width: 360px;
}

.client-default-container {
  max-width: 1480px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 641px) {
  .client-default-container {
    padding: 0 40px;
  }
}
